dd {
  margin: 0;
  -webkit-margin-start: 0;
}

.repo {
  width: calc(50% - 2rem);
  padding: 1rem;
  margin: 0.5rem;
  border: 3px solid #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 2px;
}

.repo__name {
  font-size: 0.875rem;
  font-weight: 700;
  margin-right: 0.5rem;
}

.repo__description {
  display: block;
  font-size: 0.75rem;
  font-weight: 300;
}

.repo__url {
  margin-right: 0.5rem;
}

.repo__stars {
  font-size: 0.75rem;
}

.repo__extraInfo {
  font-size: 0.75rem;
}



